var _this = this;
/*	eslint no-param-reassign: "off"	*/
var jQuery = require('jquery');
var View = require('./View');
(function ($) {
    $.fn.myPlugin = function (options) {
        var $this = _this; // dom element this
        var settings = $.extend({
            // options object
            $el: $this,
            min: 0,
            max: 1000,
            type: 'single',
            from: 100,
            to: 900,
            flag: false
        }, options);
        var view = new View($this, settings);
        // const model = new Model(settings);
        module.exports = view;
    };
})(jQuery);
